// Main javascript entry point
// Should handle bootstrapping/starting application

'use strict';

import $ from 'jquery';
import 'fullpage.js';
import 'slick-carousel';

import '../_modules/card/card';
import { buyButton, scrollerButton } from '../_modules/button/button';
import '../_modules/cart/cart';

$(() => {
	const getSectionByIndex = index => $('.section').eq(index - 1);

	const isVisible = section => $(section).is(':visible');

	const getNearestVisibleSectionIndex = (index, direction) => {
	    const step = direction === 'up' ? -1 : 1,
	        sectionsCount = $('.section').length;

	    while (index >= 0 && index < sectionsCount && !isVisible(getSectionByIndex(index))) {
	        index += step;
	    }

	    return index === sectionsCount ? sectionsCount : index;
	};

	const getAnchors = selector => {
		const anchors = selector.find('.navbar__item');
		const visibleAnchors = anchors.filter(':visible');
		const ids = [...visibleAnchors].map(anchor => anchor.dataset.helper);

		visibleAnchors.each((index, node) => {
			node.dataset.menuanchor = ids[index];
		});

		return ids;
	}

	$('#fullpage').fullpage({
		anchors: getAnchors($('#navbar')),
		menu: '#navbar',
		verticalCentered: false,
		scrollOverflow: true,
		sectionSelector: '.section',
		onLeave: function(index, nextIndex, direction){
		    const nextSection = getSectionByIndex(nextIndex);

		    if(!isVisible(nextSection)) {
		        const sectionIndex = getNearestVisibleSectionIndex(nextIndex, direction);
		        
		        if (sectionIndex !== -1) {
		            $.fn.fullpage.moveTo(sectionIndex);
		        }

		        return false;
		    }
		}
	});

	scrollerButton.on('click', event => {
		const sections = $('.section');
		const target = $(event.target.dataset.target);
		const index = sections.index(target);

		$.fn.fullpage.moveTo(index + 1);
	})

	$('.slider--promo').slick({
		dots: true
	});

	$('.slider--pricelist .slider__inner').slick({
		dots: true,
		arrow: false,
		centerMode: true,
		centerPadding: '20%',
		slidesToShow: 1,
		slidesToScroll: 1,
		infinite: false,
		prevArrow: '.controls__arrow--prev',
		nextArrow: '.controls__arrow--next',
		mobileFirst: true,
		responsive: [
			{
				breakpoint: 992,
				settings: {
					centerMode: false,
					slidesToShow: 3,
					slidesToScroll: 3,
					dots: false,
					arrow: true
				}
			}
		]
	});

	$('.slider--advantages').slick({
		dots: false,
		arrow: false,
		centerMode: true,
		centerPadding: '80px',
		slidesToShow: 1,
		slidesToScroll: 1,
		infinite: false,
		mobileFirst: true,
		responsive: [
			{
				breakpoint: 992,
				settings: {
					centerMode: false,
					slidesToShow: 4,
					slidesToScroll: 1,
					dots: false,
					arrow: false
				}
			}
		]
	});

	$('.slider--pricelist').on('beforeChange', (event, slick, currentSlide, nextSlide) => {
		const node = $('.slider__background');

		node.css('transform', `translateY(-70%) translateX(-${100 / 6 * nextSlide}%)`);
	});
});